import { useState, useEffect } from "react";
import { loadStripe } from "@stripe/stripe-js";
import {
  CardElement,
  Elements,
  useElements,
  useStripe,
} from "@stripe/react-stripe-js";
import useGetData from "../../../hooks/useGetData";
import usePostData from "../../../hooks/usePostData";
import SuccessTick from "../../../images/success-tick.png";
import FailedIcon from "../../../images/failure-icon.png";
import { useHistory } from "react-router-dom";

const CheckoutForm = ({
  planData,
  setgotoSearch,
  ActivateNewPlan,
  setPricingUrl,
  callback,
}) => {
  const stripe = useStripe();
  const elements = useElements();
  let history = useHistory();
  const [error, setError] = useState(null);
  const [cardComplete, setCardComplete] = useState(false);
  const [processing, setProcessing] = useState(false);
  const [paymentMethod, setPaymentMethod] = useState(null);
  const current_plan_value = localStorage.getItem("plan_value");
  const [isPaymentDone, setIsPaymentDone] = useState(false);
  const [isloading, setIsLoading] = useState(true);
  

  const [billingDetails, setBillingDetails] = useState({
    email: "",
    name: "",
  });
  const [isUpgraded, setIsUpgraded] = useState(false);

  //Card listing
  const [listCard, setListCard] = useState(`payment/list-cards`);
  const { data: CardListingData } = useGetData(listCard);

  //Post subscription plan
  const url = "payment/subscribe";
  const [formdata, setFormData] = useState(null);
  const {
    data,
    error: errors,
    loading,
    message: stripeMessage,
  } = usePostData(url, formdata);

  //Convert the number of billing periods in months value to "12.34" format
  function monthlyTotal(price) {
    const em1 = price.toFixed(2);
    return em1;
  }

  // Check the trial period of the particular user
  const { data: trialStatus, loading: flagLoading } = useGetData("flag-check");
  const isTrialed = trialStatus?.data?.is_trialed;

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!stripe || !elements) {
      return;
    }

    if (error) {
      elements.getElement("card").focus();
      return;
    }

    if (cardComplete) {
      setProcessing(true);
    }

    const payload = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement),
      billing_details: billingDetails,
    });

    // setProcessing(false);

    if (payload.error) {
      setError(payload.error);
    } else {
      setPaymentMethod(payload.paymentMethod);
    }
  };

  // console.log("error", error);
  // console.log("post error..", errors);
  console.log("stripeMessage..", stripeMessage?.status_code);

  const mid = paymentMethod?.id;
  const pid = planData?.stripe_price_id;

  const new_plan_value = planData?.name;
  useEffect(() => {
    if (mid) {
      setFormData({
        payment_method_id: mid,
        price_id: pid,
        // card_id: ""
      });
      setIsUpgraded(true);
    }
  }, [mid]);

  // useEffect(() => {
  //   if (isUpgraded) {
  //     localStorage.setItem("plan_value", new_plan_value);
  //   }
  // }, [isUpgraded, new_plan_value]);

  const reset = () => {
    setError(null);
    setProcessing(false);
    setPaymentMethod(null);
    setBillingDetails({
      email: "",
      name: "",
    });
  };

  const redirectHandle = () => {
    if (localStorage.getItem("redirectToJobSh")) {
      history.goBack();
      localStorage.removeItem("redirectToJobSh");
    } else {
      window.location.href = "/profile/my-plan";
    }

    if (localStorage.getItem("redirected_jobpost")) {
      history.goBack();
      localStorage.removeItem("redirected_jobpost");
    }
  };

  useEffect(() => {
    if (data) {
      setIsPaymentDone(true);
      setIsLoading(false);
      // ActivateNewPlan();
      setTimeout(() => {
        redirectHandle();
      }, 1000);
      // setPricingUrl(null);
      // setPricingUrl(`plan-details`);
    } else {
      setIsLoading(true);
    }
  }, [data]);

  const refresh = () => {
    setIsPaymentDone(false);
    // window.location.href = "/profile/my-plan";
  };

  // Passing state to my plan after the successful payment
  const planUpgrade = () => callback();
  useEffect(() => {
    if (isPaymentDone) {
      planUpgrade();
    }
  }, [isPaymentDone]);

  return (
    <>
      {isPaymentDone ? (
        stripeMessage?.status_code == 200 ? (
          <div className="Result space-y-5 p-2 md:p-8">
            <img src={SuccessTick} className="w-16 h-auto mx-auto" />
            <div className="ResultTitle" role="alert">
              Congratulation
            </div>
            <div className="ResultMessage">
              {/* Your subscription has been successfully activated! */}
              {stripeMessage.message}
            </div>
            {/* <button 
            className="rounded-lg text-lg font-semibold px-10 py-3 border border-main bg-main text-white"
            onClick={refresh}
          >Refresh</button> */}
            {/* <ResetButton onClick={reset} /> */}
          </div>
        ) : (
          <div className="Result space-y-5 p-2 md:p-8">
            <img src={FailedIcon} className="w-16 h-auto mx-auto" />
            <div className="ResultTitle" role="alert">
              Oops!!
            </div>
            <div className="ResultMessage failed">{stripeMessage.message}</div>
            {/* <button
              className="rounded-lg text-lg font-semibold px-10 py-3 border border-main bg-main text-white"
              onClick={refresh}
            >
              Refresh
            </button> */}
            {/* <ResetButton onClick={reset} /> */}
          </div>
        )
      ) : (
        <form className="payment-form" onSubmit={handleSubmit}>
          <fieldset className="FormGroup">
            <Field
              id="name"
              type="text"
              placeholder="Name on Card*"
              required
              autoComplete="name"
              value={billingDetails.name}
              onChange={(e) => {
                setBillingDetails({
                  ...billingDetails,
                  name: e.target.value,
                });
              }}
            />
            <Field
              id="email"
              type="email"
              placeholder="Your Email*"
              required
              autoComplete="email"
              value={billingDetails.email}
              onChange={(e) => {
                setBillingDetails({
                  ...billingDetails,
                  email: e.target.value,
                });
              }}
            />
          </fieldset>
          <fieldset className="FormGroup">
            <CardField
              onChange={(e) => {
                setError(e.error);
                setCardComplete(e.complete);
              }}
            />
          </fieldset>
          {error && <ErrorMessage>{error.message}</ErrorMessage>}
          {flagLoading ? (
            <span className="text-center block">Loading..</span>
          ) : (
            <SubmitButton
              processing={processing}
              error={error}
              disabled={!stripe}
            >
              {planData?.trial_period > 0 && isTrialed == false
                ? "Pay $0"
                : `Pay $${monthlyTotal(
                    planData?.monthly_price * planData?.billing_period_in_months
                  )}`}
            </SubmitButton>
          )}
        </form>
      )}
    </>
  );
};

const CARD_OPTIONS = {
  hidePostalCode: true,
  iconStyle: "solid",
  style: {
    base: {
      iconColor: "#007FEE",
      color: "rgba(55,65,81)",
      fontWeight: 500,
      fontSize: "16px",
      fontSmoothing: "antialiased",
      ":-webkit-autofill": {
        color: "rgba(55,65,81)",
      },
      "::placeholder": {
        color: "rgba(55,65,81)",
      },
    },
    invalid: {
      iconColor: "rgba(239,68,68)",
      color: "rgba(239,68,68)",
    },
  },
};

const CardField = ({ onChange }) => (
  <div className="FormRow">
    <CardElement options={CARD_OPTIONS} onChange={onChange} />
  </div>
);

const Field = ({
  id,
  type,
  placeholder,
  required,
  autoComplete,
  value,
  onChange,
}) => (
  <div className="FormRow">
    <input
      className="FormRowInput"
      id={id}
      type={type}
      placeholder={placeholder}
      required={required}
      autoComplete={autoComplete}
      value={value}
      onChange={onChange}
    />
  </div>
);

const SubmitButton = ({ processing, error, children, disabled }) => (
  <button
    className={`SubmitButton ${error ? "SubmitButton--error" : ""}`}
    type="submit"
    disabled={processing || disabled}
  >
    {processing ? "Processing..." : children}
  </button>
);

const ErrorMessage = ({ children }) => (
  <div className="ErrorMessage" role="alert">
    <svg width="16" height="16" viewBox="0 0 17 17">
      <path
        fill="#FFF"
        d="M8.5,17 C3.80557963,17 0,13.1944204 0,8.5 C0,3.80557963 3.80557963,0 8.5,0 C13.1944204,0 17,3.80557963 17,8.5 C17,13.1944204 13.1944204,17 8.5,17 Z"
      />
      <path
        fill="rgba(239,68,68)"
        d="M8.5,7.29791847 L6.12604076,4.92395924 C5.79409512,4.59201359 5.25590488,4.59201359 4.92395924,4.92395924 C4.59201359,5.25590488 4.59201359,5.79409512 4.92395924,6.12604076 L7.29791847,8.5 L4.92395924,10.8739592 C4.59201359,11.2059049 4.59201359,11.7440951 4.92395924,12.0760408 C5.25590488,12.4079864 5.79409512,12.4079864 6.12604076,12.0760408 L8.5,9.70208153 L10.8739592,12.0760408 C11.2059049,12.4079864 11.7440951,12.4079864 12.0760408,12.0760408 C12.4079864,11.7440951 12.4079864,11.2059049 12.0760408,10.8739592 L9.70208153,8.5 L12.0760408,6.12604076 C12.4079864,5.79409512 12.4079864,5.25590488 12.0760408,4.92395924 C11.7440951,4.59201359 11.2059049,4.59201359 10.8739592,4.92395924 L8.5,7.29791847 L8.5,7.29791847 Z"
      />
    </svg>
    {children}
  </div>
);

const ResetButton = ({ onClick }) => (
  <button type="button" className="ResetButton" onClick={onClick}>
    <svg width="32px" height="32px" viewBox="0 0 32 32">
      <path
        fill="#444"
        d="M15,7.05492878 C10.5000495,7.55237307 7,11.3674463 7,16 C7,20.9705627 11.0294373,25 16,25 C20.9705627,25 25,20.9705627 25,16 C25,15.3627484 24.4834055,14.8461538 23.8461538,14.8461538 C23.2089022,14.8461538 22.6923077,15.3627484 22.6923077,16 C22.6923077,19.6960595 19.6960595,22.6923077 16,22.6923077 C12.3039405,22.6923077 9.30769231,19.6960595 9.30769231,16 C9.30769231,12.3039405 12.3039405,9.30769231 16,9.30769231 L16,12.0841673 C16,12.1800431 16.0275652,12.2738974 16.0794108,12.354546 C16.2287368,12.5868311 16.5380938,12.6540826 16.7703788,12.5047565 L22.3457501,8.92058924 L22.3457501,8.92058924 C22.4060014,8.88185624 22.4572275,8.83063012 22.4959605,8.7703788 C22.6452866,8.53809377 22.5780351,8.22873685 22.3457501,8.07941076 L22.3457501,8.07941076 L16.7703788,4.49524351 C16.6897301,4.44339794 16.5958758,4.41583275 16.5,4.41583275 C16.2238576,4.41583275 16,4.63969037 16,4.91583275 L16,7 L15,7 L15,7.05492878 Z M16,32 C7.163444,32 0,24.836556 0,16 C0,7.163444 7.163444,0 16,0 C24.836556,0 32,7.163444 32,16 C32,24.836556 24.836556,32 16,32 Z"
      />
    </svg>
  </button>
);

const stripePromise = loadStripe(
  "pk_test_51KSg5aCO2z3TFkeMSCe8x5bjScYceqWbzGx3z6Z3jtsWNKiBfCPj0y5gyEzZxcpvAYZ6vPbCIS18A9dKmIROEjp6009NAxc7Fc"
);

const StripePayment = ({
  planData,
  setPricingUrl,
  ActivateNewPlan,
  callback,
}) => {
  return (
    <>
      <Elements stripe={stripePromise}>
        <CheckoutForm
          planData={planData}
          setPricingUrl={setPricingUrl}
          ActivateNewPlan={ActivateNewPlan}
          callback={callback}
        />
      </Elements>
    </>
  );
};

export default StripePayment;
