import React, { useState, useCallback, memo, useEffect } from "react";
import CreatableSelect from "react-select/creatable";
import Select, { components } from "react-select";

function SelectCreate({ benefitList, benefit, selectBenefit, benefitArray,setBenefitArray,benefitSelectRef }) {
  const InputOption = ({
    getStyles,
    Icon,
    isDisabled,
    isFocused,
    isSelected,
    children,
    innerProps,
    ...rest
  }) => {
    const [isActive, setIsActive] = useState(false);
    const onMouseDown = () => setIsActive(true);
    const onMouseUp = () => setIsActive(false);
    const onMouseLeave = () => setIsActive(false);

    // styles
    let bg = "transparent";
    if (isFocused) bg = "#eee";
    if (isActive) bg = "#B2D4FF";

    const style = {
      alignItems: "center",
      backgroundColor: bg,
      color: "inherit",
      display: "flex ",
    };

    // prop assignment
    const props = {
      ...innerProps,
      onMouseDown,
      onMouseUp,
      onMouseLeave,
      style,
    };
  
    const removeSelected = (data) => {
      if(benefitArray?.map(a => a.name).includes(children) && isCheck){
        let temp = benefitArray.filter((item) => item.name !== data)
        var uniq={}
        var arrFiltered = temp.filter(obj => !uniq[obj.name] && (uniq[obj.name] = true));
        setBenefitArray(arrFiltered)
      }
    }
    const [isCheck , setIsCheck] = useState(benefitArray?.map(a => a.name).includes(children))
    return (
      <components.Option
        {...rest}
        isDisabled={isDisabled}
        isFocused={isFocused}
        isSelected={isSelected}
        getStyles={getStyles}
        innerProps={props}
      >
        <input type="checkbox" checked={isCheck} 
        className="mx-2"
        onChange={()=>{removeSelected(children)}}
        />
        {/* {console.log(benefitArray.map(a => a.name).includes(children), "children")} */}
        {children}
      </components.Option>
    );
  };
  const [options, setOptions] = useState([]);
  const SelectStyles = {
    option: (provided, state) => ({
      ...provided,
      display: "flex",
      textAlign: "left", 
    }),
    multiValue: (provided, state) => ({
      ...provided,
    }),
    multiValueLabel: (provided, state) => ({
      ...provided,
      display: "none",
    }),
    multiValueRemove: (provided, state) => ({
      ...provided,
      display: "none",
    }),
  };
  const handleCreate = useCallback(
    (inputValue) => {
      const newValue = { name: inputValue };
      setOptions((preOptions) => [...preOptions, newValue]);
      // selectBenefit(newValue);
    },
    [options]
  );
  const selectBenefits=(e)=>{
    setBenefitArray([...benefitArray,...e])
  }

  useEffect(() => {
    setOptions(benefitList ? benefitList.data?.response_data : []);
  }, [benefitList]);

  return (
    <div className="App">
      <CreatableSelect
        ref={benefitSelectRef}
        styles={SelectStyles}
        className="react-select-container"
        classNamePrefix="react-select"
        maxMenuHeight={205}
        getOptionLabel={(option) => option.name}
        getOptionValue={(option) => option.id}
        options={benefitList?Object.values(benefitList?.data?.response_data.reduce((acc,cur)=>Object.assign(acc,{[cur.name]:cur}),{})):{}}
        isClearable
        closeMenuOnSelect={false}
        hideSelectedOptions={false}
        isMulti
        // value={benefit}
        onChange={(e)=>{selectBenefits(e)}}
        onCreateOption={handleCreate}
        components={{
          Option: InputOption,
        }}
        getNewOptionData={(inputValue, optionLabel) => ({
          id: inputValue,
          name: optionLabel,
        })}
      />
    </div>
  );
}

export default memo(SelectCreate);
